import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css'],
})
export class NavbarComponent implements OnInit {
  constructor() {}
  toggleTheme() {
    document.body.classList.toggle('light');
    document.body.classList.toggle('dark');
    let themeTogglerLabel = document.getElementById('themeTogglerLabel');
    themeTogglerLabel?.classList.toggle('on');
    themeTogglerLabel?.classList.toggle('off');
  }
  toggleMenu() {
    let navbarMenu = document.getElementById('navbarMenu');
    navbarMenu?.classList.toggle('app-ham-menu-expanded');
    document.getElementById('menu')?.classList.toggle('collpased');
  }
  ngOnInit(): void {}
}
