import { Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';
import { NgbCarousel } from '@ng-bootstrap/ng-bootstrap';
import { Kurti } from 'src/app/Model/ResponseModel/kurti.model';

@Component({
  selector: 'app-kurti-view',
  templateUrl: './kurti-view.component.html',
  styleUrls: ['./kurti-view.component.css'],
})
export class KurtiViewComponent implements OnInit {
  @Input()
  kurti: Kurti = new Kurti(
    1,
    ['some link'],
    'some title',
    'mst hai le kle ',
    'cotton',
    524,
    false
  );
  @ViewChild('kurtiCarousel') kurtiCarousel!: NgbCarousel;
  constructor() {}

  onFavourite() {
    console.log(this.kurti.id);
    this.kurti.isFavourite = !this.kurti.isFavourite;
  }
  ngAfterViewInit() {
    this.kurtiCarousel.pause();
  }
  handleCarouselMouseEvent(
    component: KurtiViewComponent,
    isMouseEnterEvent: boolean
  ) {
    console.log('isMouseEnterEvent :', isMouseEnterEvent);
    console.log('component :', component);
    if (isMouseEnterEvent)
      component.kurtiCarousel.cycle();
    else
      component.kurtiCarousel.pause();
  }
  ngOnInit(): void {}
}
