export class Kurti {
  public id:number;
  public title: String;
  public imageLinks: String[];
  public description: String;
  public fabric: String;
  public price: number;
  public isFavourite: boolean;

  constructor(
    id:number,
    imageLink: String[],
    title: String,
    description: String,
    fabric: String,
    price: number,
    favourite:boolean
  ) {
    this.id = id;
    this.imageLinks = imageLink;
    this.title = title;
    this.description = description;
    this.fabric = fabric;
    this.price = price;
    this.isFavourite = favourite;
  }
}
